import React from "react";
import { Cell, Div, Avatar, Link } from "@vkontakte/vkui";
import Icon12Fire from "@vkontakte/icons/dist/12/fire";
import styles from "../styles/custom-styles.sass";
import Icon12Verified from "@vkontakte/icons/dist/12/verified";
import { getRankByRating, formatCoins } from "../helpers";

const ListUsers = ({
  id,
  firstName,
  lastName,
  rating,
  photoURL,
  amount,
  idVk,
  ranks,
  isVIP,
  vipNameColor,
  openMenuVIP,
}) => {
  const urlUser = "https://vk.com/id" + idVk;

  return (
    <Div className="card">
      <span className="listNumber">{id}</span>

      <Cell
        className="myItem"
        before={
          photoURL ? (
            <Link href={urlUser} target="_blank">
              <Avatar src={photoURL} />
            </Link>
          ) : (
            <Avatar src={"https://vk.com/images/camera_200.png"} />
          )
        }
        description={
          <Div
            style={{
              marginLeft: "10px",
              padding: 0,
            }}
          >
            <Div
              style={{
                padding: 2,
                marginLeft: 0,
                display: "flex",
                alignItems: "start",
                flexDirection: "row",
              }}
            >
              {/* <Icon12Fire fill="red"/> */}
              <Div className="iconStar" />
              <Div className="div" style={{ whiteSpace: "nowrap" }}>
                {rating} ({getRankByRating(rating, ranks)})
              </Div>
            </Div>
            <Div
              style={{
                padding: 2,
                margin: 0,
                display: "flex",
                alignItems: "start",
                flexDirection: "row",
              }}
            >
              {/* <Icon12Fire fill="red"/> */}
              <Div className="iconDC" />
              <Div style={{ padding: 0 }}>{formatCoins(amount)}</Div>
            </Div>
          </Div>
        }
        // asideContent={<Icon24FavoriteOutline fill="black" />}
        // before={<Icon24FavoriteOutline/>}
        // indicator={rating}
        // indicator={<Icon24FavoriteOutline fill="black"/>}
      >
        <Div
          style={{
            display: "flex",
            justifyContent: "start",
            alignItems: "center",
            flexDirection: "row",
            marginLeft: "10px",
          }}
        >
          {isVIP ? (
            <>
              <span
                style={{
                  color: [, "green", "#D40000", "blue", "#800080"][
                    vipNameColor
                  ],
                }}
              >
                {firstName + " " + lastName}
              </span>

              <Icon12Verified
                onClick={openMenuVIP}
                style={{ marginLeft: "5px" }}
                className="animationIcons"
                fill={[, "green", "#D40000", "blue", "#800080"][vipNameColor]}
                width={16}
                height={16}
              />
            </>
          ) : (
            firstName + " " + lastName
          )}
        </Div>
      </Cell>
    </Div>
  );
};

export { ListUsers };
